<template>
  <div class="home">
    <h1>Remove names you don't like</h1>
    <h2>Click on a name to remove it from the list</h2>
  </div>
  <div class="names">
    <div class="name" v-for="name in names" :key="name" @click="removeName(name)">
      {{ name }}
    </div>
  </div>
</template>

<script>

export default {
  name: 'HomeView',
  data() {
    return {
      names: [],
      vovels: ['a', 'e', 'i', 'o', 'u'],
      consonants: ['b', 'c', 'd', 'f', 'g', 'h', 'j', 'k', 'l', 'm',
        'n', 'p', 'q', 'r', 's', 't', 'v', 'w', 'x', 'y', 'z'],
      option: ['vcvcv', 'cvcvc', 'ccvcv', 'cvvcv', 'cvcvcv', 'vcvcvc', 'cvccv', 'vccvc', 'cvccvc', 'vcvcc']
    }
  },
  methods: {
    removeName(name) {
      this.names = this.names.filter(n => n !== name)
      this.generateName()
    },
    generateName() {
      const option = this.option[Math.floor(Math.random() * this.option.length)]
      const name = option.split('').map(letter => {
        if (letter === 'v') {
          return this.vovels[Math.floor(Math.random() * this.vovels.length)]
        } else {
          return this.consonants[Math.floor(Math.random() * this.consonants.length)]
        }
      }).join('')
      this.names.push(name)
    }
  }, mounted() {
    //repeate 10 times
    for (let i = 0; i < 10; i++) {
      this.generateName()
    }
  }
}
</script>

<style scoped>
.names {
  display: flex;
  width: 55vw;
  margin: auto;
  flex-wrap: wrap;
  justify-content: space-around;
}

.name {
  margin: 10px;
  background-color: #333;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  width: 25vw;
  height: 5vw;
  font-size: 4vw;
  text-align: center;
}

.name:hover {
  background-color: #444;
}

.name:active {
  background-color: #555;
}

h1 {
  font-size: xx-large;
}

h2 {
  font-size: x-large;
}
</style>

